import axios from "axios";
import { viewToastSuccess, viewToastError } from "../helper";
import { RegisterInfo } from "../interface";

export const baseURL =
  "https://port-0-unicoop-nx562olfpi8ozh.sel3.cloudtype.app";

export const setHeaders = (token: string | null) => {
  axios.defaults.headers.common = token
    ? {
        "Access-Control-Allow-Origin": window.location.origin,
        Authorization: `Bearer ${token}`,
      }
    : {
        "Access-Control-Allow-Origin": window.location.origin,
      };
};

export const swrFetcher = async (url: string) => {
  return (await axios.get(url)).data;
};

export const authApi = {
  sendCode: async (email: string) => {
    try {
      const response = await axios.post("/auth/email", {
        email,
      });
      if (response.status === 200) {
        viewToastSuccess("   .");
      }
    } catch (e) {
      if (axios.isAxiosError(e) && e.response) {
        const statusCode = e.response.status;
        if (statusCode === 409) {
          viewToastError(
            "       ."
          );
        } else if (statusCode === 500) {
          viewToastError(
            "  .    ."
          );
        } else {
          viewToastError(
            "   .    ."
          );
        }
      } else {
        viewToastError(
          "    .    ."
        );
      }
    }
  },

  verifyCode: async ({
    isPasswordValid,
    passwordConfirm,
    registerInfo,
    changeBoxContent,
  }: {
    isPasswordValid: boolean;
    passwordConfirm: string;
    registerInfo: RegisterInfo;
    changeBoxContent: () => void;
  }) => {
    const { name, password, verifyCode, email } = registerInfo;

    if (name === "") {
      viewToastError(" .");
      return;
    } else if (!password || !isPasswordValid || password !== passwordConfirm) {
      viewToastError("   .");
      return;
    }
    try {
      await axios.post("/auth/verify", {
        email,
        password,
        verifyCode,
        name,
      });
      viewToastSuccess(" .");
      setTimeout(() => {
        changeBoxContent();
      }, 3000);
    } catch (e) {
      viewToastError("  .");
    }
  },

  getMyInfo: async (token: string) => {
    try {
      setHeaders(token);

      return await axios.get("/auth/user").then((res) => {
        return res.data;
      });
    } catch (e) {
      console.log(e);
      throw new Error("Invalid Token");
    }
  },

  login: async ({ email, password }: { email: string; password: string }) => {
    try {
      return await axios
        .post("/auth/login", {
          email,
          password,
        })
        .then((res) => {
          if (res.data.code === 1) {
            localStorage.setItem("token", res.data.token);
            return res.data.token;
          }
        });
    } catch (e) {
      if (axios.isAxiosError(e) && e.response) {
        console.log(e.response.data.message);
        viewToastError(e.response.data.message);
        throw new Error(e.response.data.message);
      }
    }
  },

  deleteAccount: async (email: string) => {
    try {
      const response = await axios.delete(`/auth/user?email=${email}`);
      if (response.status === 200) {
        viewToastSuccess("   .");
      }
    } catch (e) {
      if (axios.isAxiosError(e) && e.response) {
        const statusCode = e.response.status;
        if (statusCode === 404) {
          viewToastError("  .");
        } else if (statusCode === 500) {
          viewToastError(
            "  .    ."
          );
        } else {
          viewToastError(
            "   .    ."
          );
        }
      } else {
        viewToastError(
          "    .    ."
        );
      }
    }
  },
};
